import React, {useEffect,useState} from "react";
import { graphql } from "gatsby";
import Footer2 from '../Footer/Footer2';
import Header2 from '../Header/Header2';

import { PortfolioProvider } from '../../context/context';

import {footerData } from '../../mock/data';
import '../../style/main.scss';


export default function Template({
  data, 
}) {
  const { markdownRemark } = data 
  const { frontmatter, html } = markdownRemark

  const [footer, setFooter] = useState({});
  useEffect(() => {
    setFooter({ ...footerData });
  }, []);

  return (
    <div className="wrapper">
      <Header2 />
      <div id="top" className="blog-post-container">
        <div className="blog-post">
          <h1 className="posttitle">{frontmatter.title}</h1>
          <div className="metasection row half">
            <div className="col-4">
              <p className="metaheading">Roles</p>
              <p className="metatext">{frontmatter.role}</p>
            </div>
            <div className="col-4">
              <p className="metaheading">Timeline</p>
              <p className="metatext">{frontmatter.time}</p>
            </div>
            <div className="col-4">
              <p className="metaheading">Location</p>
              <p className="metatext">{frontmatter.loc}</p>
            </div>
          </div>
          <div
            className="blog-post-content"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </div>
      </div>
      <PortfolioProvider value={footer}>
        <Footer2 />
      </PortfolioProvider>
    </div>
    
    
  )
}

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        slug
        title
        role
        loc
        time
      }
    }
  }
`