import React, { useEffect } from 'react';

// import { Link, useLocation} from "react-router-dom";
// import Scroll from "react-scroll";
import '../../style/main.scss'
import { AnchorLink } from "gatsby-plugin-anchor-links";


const Header2=() => {

    const [scrolled,setScrolled]=React.useState(false); 
    
    

    const handleScroll=() => {
      const offset=window.scrollY;
      if(offset > 200 ){
        setScrolled(true);
      }
      else{
        setScrolled(false);
      }
    }
    useEffect(()=> {
      window.addEventListener('scroll',handleScroll)
    })

    let navbarClasses=['navbar'];
    if(scrolled){
      navbarClasses.push('scrolled');
    }

    return (
      <header className={navbarClasses.join(" ")}>
        <div className="logo">
            <span className="logo">
                <a href="/" className="headerlink">
                    <span className="alt">SARTHAK MARWAHA</span>
                </a>
            </span>
        </div>

        <nav className="navigation">
            <span className="nav">
                <AnchorLink to="/#about" className="headerlink">
                <div className="item text-color-alt">About</div>
                </AnchorLink>
                <AnchorLink to="/#Projgallery" className="headerlink">
                <div className="item text-color-alt">Projects</div>
                </AnchorLink>
                <AnchorLink to="/#contact" className="headerlink">
                <div className="item text-color-alt">Contact</div>
                </AnchorLink>
            </span>
        </nav>
      </header>
    )
};
  export default Header2;